@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
:root {
  --transition: all 200ms ease-out;
}

:focus {
  outline: none;
}

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.5px;
  -webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.docs {
    display: flex;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.docs-nav {
    min-width: 250px;
    display: flex;
    flex-direction: column;
    background: rgb(242, 242, 242);
    overflow: hidden;
}

.docs-nav-header, .docs-nav-list {
    display: flex;
    flex-direction: column;
}

.docs-nav-list {
    overflow-y: auto;
}

.docs-nav-header span {
    font-size: 18px;
    letter-spacing: 1px;
    margin: 20px 0 18px 0;
    text-align: center;
    text-transform: uppercase;
}

.docs-nav-header input {
    background: rgba(214, 214, 214, 0.5);
    border: none;
    letter-spacing: 1px;
    padding: 14px;
    margin-bottom: 5px;
    height: 40px;
    color: #a49d98;
    font-size: 15px;
}

.docs-nav-list span {
    background-color: unset;
    cursor: pointer;
    text-align: left;
    font-size: 16px;
    border-bottom: 1px solid rgba(214, 214, 214, 0.5);
    padding: 10px 16px;
}

.docs-nav-list span:hover {
    background: #d6d6d6;
}

.docs-content {
    padding: 16px 60px;
    overflow-y: auto;
    flex: 1 1 auto;
}
.avatar {
    background: #eeeeee;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.avatar>img {
    border-radius: 50%;
}

.avatar span {
    /* background: #eeeeee; */
}

/*sizes*/

.avatar span.xs {
    line-height: 24px;
}

.avatar span.sm {
    line-height: 32px;
}

.avatar span.md {
    line-height: 48px;
}

.avatar span.lg {
    line-height: 64px;
}

.avatar span.xl {
    line-height: 120px;
}

.avatar.xs, .avatar img.xs {
    width: 24px;
    height: 24px;
}

.avatar.sm, .avatar img.sm {
    width: 32px;
    height: 32px;
}

.avatar.md, .avatar img.md, .avatar span.md {
    width: 48px;
    height: 48px;
}

.avatar.lg, .avatar img.lg {
    width: 64px;
    height: 64px;
}

.avatar.xl, .avatar img.xl {
    width: 120px;
    height: 120px;
}
.simple-button {
  padding: 10px 15px;
  border: none;
  color: white;
  border-radius: 6px;
}

/*colors*/
.simple-button.red {
  background: red;
}

.simple-button.blue {
  background: blue;
}

.simple-button.black {
  background: red;
}

/*sizes*/
.simple-button.sm {
  font-size: 16px;
}

.simple-button.md {
  font-size: 20px;
}

.simple-button.lg {
  font-size: 24px;
}

